.SearchBar {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
  margin-bottom: 2rem;
  text-align: center;
 
}

.SearchBar input {
  width: 250px;
  padding: .88rem 0;
  border: 1px solid #fff;
  border-radius: 10px;
  margin-bottom: 1rem;
  color: #010c3f;
  text-align: center;
  font-size: 1rem;
  box-shadow:5px 5px 15px rgba(65, 138, 221, 0.7);
  color:darkslategray;

}

.SearchBar input:focus {
  outline: none;
}

.SearchButton {
  cursor: pointer;
  width: 8.11rem;
  padding: .77rem 0;
  border-radius: 54px;
  background-color: #010c3f;
  text-align: center;
  font-size: .833rem;
  transition: background-color .25s;
  border: 0px;
  color: #fff;
  font-weight: 500;
  box-shadow:5px 5px 15px rgba(65, 138, 221, 0.7);
  margin-bottom: 1rem;
}

.SearchButton:hover {
  background-color: rgba(158, 127, 250, 0.7);
}