.SearchResults {
  text-align: center;
  width: 45%;
  height: 80vh;
  overflow-y: scroll;
  padding: .88rem;
  box-shadow:5px 5px 15px rgba(65, 138, 221, 0.7);
}
.SearchResults h2{
  color: antiquewhite;
  display: flex;
  text-align: center;
  justify-content: center;
   padding-right: 9px;
  margin-top: 30px;
  margin-bottom: 17px;
}

::-webkit-scrollbar {
    width: 0px;
    background: transparent;
}

@media only screen and (max-width: 1020px) {
  .SearchResults {
    width: 90%;
    margin-bottom: 2rem;
  }
}