.Track {
  display: flex;
  align-items: center;
  max-height: 200px;
  border-bottom: 1px solid rgba(256, 256, 256, 0.8);
}

.Track-action {
  cursor: pointer;
  padding: .5rem;
  font-size: .9rem;
  transition: color .25s;
  border: 0px;
  background-color: rgba(0, 0, 0, 0);
  color: #fff;
  
}

.Track-action:hover {
  color: rgba(265, 265, 265, .5);
}

.Track-information {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  max-height: 120px;
   
}

.Track-information h3 {
  margin: 5px auto;
}
.Track-information h3:hover{
  color: black;
}
.Track-information p {
  font-size: .83rem;
  font-weight: 300;
  color: rgba(256, 256, 256, 0.8);
  margin: 3px auto;
}
.Track-information p:hover{
  color: black;
}
.Track-information a{
 color: aliceblue;
 text-decoration: none;
 font-weight: bolder;
 text-shadow: 2px 2px 3px lightcyan;
 position: relative;
 font-size: small;
 margin: 3px auto;
}
.Track-information a:hover{
  text-shadow: 5px 3px 2px rgb(161, 247, 241);
  color: antiquewhite;

}